.main {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

.main__subtitle {
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
  position: relative;
  margin: 0;
  padding: 0;
}

.main__subtitle::after {
  content: ' ';
  background: black;
  width: 100%;
  height: 1px;
  top: 50px;
  left: 0px;
  position: absolute;
  margin: 0;
  padding: 0;
}

@media (max-width: 615px) {
  .main__subtitle {
    font-size: 18px;
    letter-spacing: -0.72px;
  }
}
